import * as React from "react";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { useNavigate } from "react-router-dom";

import GroupIcon from "@mui/icons-material/Group";
import CorporateFareIcon from "@mui/icons-material/CorporateFare";

import styles from "./card.module.css";

import { Grid } from "@mui/material";

import Status from "../custom/status/status";

const bull = (
  <Box component="span" sx={{ display: "inline-block", mx: "2px", transform: "scale(0.8)" }}>
    •
  </Box>
);

export default function BasicCard({ items }) {
  const navigate = useNavigate();
  const navigateToFamilyPage = () => {
    navigate({ pathname: `/app/family/${items._id}` });
  };

  return (
    <Card style={{ direction: "rtl" }} sx={{ minWidth: 275 }}>
      <CardContent>
        <Box sx={{ display: "flex", justifyContent: "space-between" }}>
          <Typography className={styles.magic_box} sx={{ fontSize: 14 }} gutterBottom>
            واحد {items.house.unit_name}
          </Typography>
          <Typography className={styles.sub_card} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
            <GroupIcon />
            {items.family_name}
          </Typography>
          <Typography className={styles.sub_card} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
            <CorporateFareIcon />
            {items.members_count} نفر
          </Typography>
        </Box>
      </CardContent>
      <CardActions sx={{ display: "flex", flexDirection: "row-reverse" }}>
        <Button
          variant="contained"
          onClick={() => {
            navigateToFamilyPage();
          }}
          size="small"
        >
          بیشتر
        </Button>
      </CardActions>
    </Card>
  );
}
