import * as React from "react";

const AppContext = React.createContext({
  lang: "en",
  theme: "dark",
});

const AppContextProvider = (props) => {
  const localTheme = localStorage.getItem("theme");
  const localLang = localStorage.getItem("en");

  const [theme, setTheme] = React.useState(localTheme ? localTheme : "dark");
  const [lang, setLang] = React.useState(localLang ? localLang : "en");

  React.useEffect(() => {
    localStorage.setItem("theme", theme);
  }, [theme]);

  return <React.Fragment>{<AppContext.Provider value={{ theme, setTheme, lang, setLang }}>{props.children}</AppContext.Provider>}</React.Fragment>;
};

export { AppContextProvider, AppContext };
