import React, { useContext } from "react";

import Wrapper from "../../../components/wrapper/Wrapper";
import { Switch } from "@mui/material";
import { Grid } from "@mui/material";
import Box from "@mui/material/Box";

import { DatePicker } from "jalali-react-datepicker";
import { CssBaseline } from "@mui/material";

import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";

import { AppContext } from "../../../utils/provider";

const Setting = () => {
  const { lang, theme, setTheme } = useContext(AppContext);

  const handleChangeTheme = () => {
    const isDarkMode = theme === "dark";
    if (isDarkMode) setTheme("light");
    else setTheme("dark");
  };

  return (
    <Wrapper>
      <CssBaseline>
        <Grid xs={12} lg={6}>
          <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
            Dark Mode
            <Switch checked={theme === "dark"} onClick={() => handleChangeTheme()} id="standard-basic" variant="standard" />
          </Box>
        </Grid>
        <Grid xs={12} lg={6}>
          <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
            language
            <Box sx={{ width: "170px" }}>
              <FormControl fullWidth>
                <InputLabel id="demo-simple-select-label">language</InputLabel>
                <Select labelId="demo-simple-select-label" id="demo-simple-select" value={"fa"} label="Age" onChange={() => {}}>
                  <MenuItem value={"fa"}>persian</MenuItem>
                  <MenuItem value={"en"}>English</MenuItem>
                </Select>
              </FormControl>
            </Box>
          </Box>
        </Grid>
      </CssBaseline>
    </Wrapper>
  );
};

export default Setting;
