import React, { useContext } from "react";

import { Routes, Route, Navigate } from "react-router-dom";
import Nav from "./app/nav/Nav";
import Header from "./app/header/Header";

import { ThemeProvider, createTheme } from "@mui/material/styles";

import Home from "./app/home/Home";
import Setting from "./app/setting/Setting";
import Bill from "./app/bill/Bill";
import Manage from "./app/manage/manage";
import FamilyInfo from "./app/family/familyInfo";
import BillPage from "./app/bill/billPage";

import { AppContext } from "../utils/provider";

const Index = (props) => {
  const appContext = useContext(AppContext);

  const darkTheme = createTheme({
    palette: {
      mode: appContext.theme,
    },
  });

  function urlBase64ToUint8Array(base64String) {
    var padding = "=".repeat((4 - (base64String.length % 4)) % 4);
    var base64 = (base64String + padding).replace(/\-/g, "+").replace(/_/g, "/");

    var rawData = window.atob(base64);
    var outputArray = new Uint8Array(rawData.length);

    for (var i = 0; i < rawData.length; ++i) {
      outputArray[i] = rawData.charCodeAt(i);
    }
    return outputArray;
  }

  var reg;

  const configPushSubscription = () => {
    if (!("serviceWorker" in navigator)) return;
    navigator.serviceWorker.ready
      .then((SWREG) => {
        reg = SWREG;
        return SWREG.pushManager.getSubscription();
      })
      .then((res) => {
        if (res === null) {
          const publicVapidKey = "BIwv9ILtDi-0RLRE7tvufB0QbA7fLF8zKb0s1MH4wHYKtg7yh-Y-Gt4cOV5DrpHqJ2jgAckndMPhz3aU4mFFCkU";
          const convertedKey = urlBase64ToUint8Array(publicVapidKey);
          reg.pushManager.subscribe({
            userVisibleOnly: true,
            applicationServerKey: convertedKey,
          });
        }
      });
  };

  Notification.requestPermission().then((res) => {
    console.log(res);
  });
  return (
    <div>
      <ThemeProvider theme={darkTheme}>
        <Header />
        <div>
          <Routes>
            <Route path="/settings" element={<Setting />} />
            <Route path="/bills/add-bill" element={<Bill />} />
            <Route path="/bills" element={<BillPage />} />
            <Route index path="/home" element={<Home />} />
            <Route index path="/family/:id" element={<FamilyInfo />} />
            <Route path="/manage" element={<Manage />} />
          </Routes>
        </div>
        <Nav />
      </ThemeProvider>
    </div>
  );
};

export default Index;
