import React from "react";

import { Button, Slide, Dialog, DialogContent, Grid, Box, TextField, CssBaseline } from "@mui/material";

import styles from "./familyInfo.module.css";

const style = {
  width: "400px",
  height: "40px",
  display: "flex",
  justifyContent: "space-between",
};

const BillInfo = ({ bill, onClose }) => {
  return (
    <div>
      <Box sx={style}>
        <span> Start Date : </span>
        <span className={styles.persian_font}>{new Date(bill.date_from).toLocaleDateString("fa")}</span>
      </Box>
      <Box sx={style}>
        <span>End Date :</span> <span className={styles.persian_font}>{new Date(bill.date_to).toLocaleDateString("fa")}</span>
      </Box>
      <Box sx={style}>
        <span>Dead Line Date :</span> <span className={styles.persian_font}>{new Date(bill.dead_line_date).toLocaleDateString("fa")}</span>
      </Box>
      <Box sx={style}>
        <span>Issue Date :</span> <span className={styles.persian_font}>{new Date(bill.issue_date).toLocaleDateString("fa")}</span>
      </Box>
      <Box sx={style}>
        <span>Consumtion Amount :</span> <span>{bill.consumtion_amount}</span>
      </Box>
      <Box sx={style}>
        <span>tax :</span> <span>{bill.tax.toLocaleString("en-US")}</span>
      </Box>
      <Box sx={style}>
        <span> Main Bill Price :</span> <span>{bill.main_price.toLocaleString("en-US")}</span>
      </Box>
      <Box sx={style}>
        <span> Type :</span> <span>{bill.type}</span>
      </Box>
      <Button onClick={() => onClose(false)}>close</Button>
    </div>
  );
};

export default BillInfo;
