import React, { useState, useEffect } from "react";
import { CssBaseline } from "@mui/material";
import Wrapper from "../../../components/wrapper/Wrapper";
import customAxiox from "../../../utils/axios";

import { useNavigate } from "react-router-dom";

import styles from "./bill.module.css";

import AddCardIcon from "@mui/icons-material/AddCard";

import Paper from "@mui/material/Paper";
import { Grid, TableContainer, Table, TableRow, TableBody, TableHead, TableCell, Button, ButtonGroup, Chip } from "@mui/material";

const payTag = (isPaid = false) => {
  if (isPaid) {
    return <Chip label="paid" color="success" variant="filled" />;
  } else {
    return <Chip label="not paid" color={"error"} variant="filled" />;
  }
};

const changeBillStatus = (billId, status) => {
  customAxiox("/bill", {
    method: "PATCH",
    data: { _id: billId, is_paid: status },
  }).then((res) => {
    console.log(res);
  });
};

const data = [
  {
    _id: "lkajsdlfjlasdlfjalsjkdf",
    date_from: "2022-04-24T00:00:00.000+00:00",
    date_to: "2022-05-16T00:00:00.000+00:00",
    issue_date: "2022-06-20T00:00:00.000+00:00",
    dead_line_date: "2022-06-07T00:00:00.000+00:00",
    is_paid: false,
    type: "water",
    bill_id: "453",
    main_price: 950000,
    tax: 453453,
    previus_debt_price: 453,
    consumtion_amount: 453,
    discount: 453,
  },
];

const BillPage = () => {
  const navigate = useNavigate();
  const [billsData, setBillsData] = useState([]);

  useEffect(() => {
    customAxiox.get("/bill").then((res) => {
      setBillsData(res.data);
    });
  }, []);

  const changeBillStatus = (billId, status) => {
    customAxiox("/bill", {
      method: "PATCH",
      data: { _id: billId, is_paid: status },
    }).then((res) => {
      console.log(res);
      customAxiox.get("/bill").then((res) => {
        setBillsData(res.data);
      });
    });
  };

  const deleteBill = (billId) => {
    alert("Warnning : deleting bill Data will Erase all the related information such as payment with the same bill id !");
    customAxiox
      .delete("/bill", {
        data: { _id: billId },
      })
      .then((res) => {
        console.log(res);
        customAxiox.get("/bill").then((res) => {
          setBillsData(res.data);
        });
      });
  };

  return (
    <CssBaseline>
      <Wrapper>
        <Grid container padding={1}>
          <Button variant="contained" onClick={() => navigate("add-bill")} startIcon={<AddCardIcon />}>
            Add New bill
          </Button>
        </Grid>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              <TableRow style={{ backgroundColor: "#1769aa" }}>
                <TableCell align="left">
                  <b>bill price</b>
                </TableCell>
                <TableCell align="right">
                  <b> date from</b>
                </TableCell>
                <TableCell align="right">
                  <b>date to</b>
                </TableCell>
                <TableCell color="red" align="right">
                  <b>type</b>
                </TableCell>
                <TableCell align="right">
                  <b>is paid</b>
                </TableCell>
                <TableCell align="right">
                  <b>actions</b>
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {billsData.map((row, index) => (
                <TableRow
                  style={{ backgroundColor: index % 2 ? "rgba(100,100,100,0.1)" : "" }}
                  key={row._id}
                  sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                >
                  <TableCell align="left">{Number(row.main_price).toLocaleString("en")}</TableCell>
                  <TableCell align="right">
                    <span className={styles.persian_font}> {new Date(row.date_from).toLocaleDateString("fa")}</span>
                  </TableCell>
                  <TableCell align="right">
                    <span className={styles.persian_font}> {new Date(row.date_to).toLocaleDateString("fa")}</span>
                  </TableCell>
                  <TableCell align="right">{row.type}</TableCell>
                  <TableCell align="right">{payTag(row.is_paid)}</TableCell>
                  <TableCell align="right">
                    <ButtonGroup variant="contained" aria-label="text button group">
                      <Button onClick={() => deleteBill(row._id)}>delete</Button>
                      <Button onClick={() => changeBillStatus(row._id, !row.is_paid)}>{row.is_paid ? "un_pay" : "pay"}</Button>
                    </ButtonGroup>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Wrapper>
    </CssBaseline>
  );
};

export default BillPage;
