import React, { useEffect, useState } from "react";

import Wrapper from "../../../components/wrapper/Wrapper";
import CustomDatePicker from "../../../components/custom/datePicker/datePicker";
import customAxiox from "../../../utils/axios";

import style from "./bill.module.css";

import { Button, Slide, Dialog, DialogContent, Grid, Box, TextField, CssBaseline } from "@mui/material";

import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";

import { convertNumberToString } from "../../../utils/NumToStr";

import BillInfo from "./billsInfo";

const Bill = () => {
  const [billType, setBillType] = useState("water");
  const [openModal, setOpenModal] = useState(true);

  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [issueDate, setIssueDate] = useState("");
  const [deadLineDate, setDeadLineDate] = useState("");

  const [billId, setBillId] = useState("");
  const [billIdErr, setBillIdErr] = useState("");

  const [mainPrice, setMainPrice] = useState("");
  const [mainPriceStr, setMainPriceStr] = useState("");
  const [mainPriceErr, setMainPriceError] = useState("");

  const [tax, setTax] = useState("");
  const [taxErr, setTaxErr] = useState("");

  const [previusDebt, setPreviusDebt] = useState("");
  const [previusDebtErr, setPreviusDebtErr] = useState("");

  const [discount, setDiscount] = useState("");
  const [discountErr, setDiscountErr] = useState("");

  const [consumtionAmount, setConsumtionAmount] = useState("");
  const [consumtionAmountErr, setConsumtionAmountErr] = useState("");

  useEffect(() => {
    setMainPriceStr(convertNumberToString(Number(mainPrice)));
  }, [mainPrice]);

  const onSubmit = (e) => {
    e.preventDefault();

    if (mainPrice.trim().length === 0 || isNaN(Number(mainPrice))) {
      setMainPriceError("invalid Main Price");
    } else {
      setMainPriceError("");
    }
    if (discount.trim().length === 0 || isNaN(Number(discount))) {
      setDiscountErr("Invalid Discount");
    } else {
      setDiscountErr("");
    }
    if (previusDebt.trim().length === 0 || isNaN(Number(previusDebt))) {
      setPreviusDebtErr("Invalid debt amount");
    } else {
      setPreviusDebtErr("");
    }

    if (tax.trim().length === 0 || isNaN(Number(tax))) {
      setTaxErr("Invalid Tax amount");
    } else {
      setTaxErr("");
    }

    if (billId.trim().length === 0) {
      setBillIdErr("invalid Bill id");
    } else {
      setBillIdErr("");
    }

    if (consumtionAmount.trim().length === 0 || isNaN(Number(consumtionAmount))) {
      setConsumtionAmountErr("Invalid consumtion amount ");
    } else {
      setConsumtionAmountErr("");
    }

    if (startDate && endDate && issueDate && deadLineDate && billId && mainPrice && tax && previusDebt && discount && consumtionAmount) {
      const data = {
        date_from: startDate.slice(0, 10),
        date_to: endDate.slice(0, 10),
        issue_date: issueDate.slice(0, 10),
        dead_line_date: deadLineDate.slice(0, 10),
        type: billType,
        bill_id: billId,
        is_paid: false,
        main_price: Number(mainPrice),
        tax: Number(tax),
        previus_debt_price: Number(previusDebt),
        consumtion_amount: Number(consumtionAmount),
        discount: Number(discount),
      };
      customAxiox("/bill", {
        method: "POST",
        data: data,
      })
        .then((res) => {
          alert("data successfully added");
          console.log(res);
        })
        .catch((err) => {
          alert("some error happened see console for more detail");
          console.log(err);
        });
    }
  };

  return (
    <Wrapper>
      <CssBaseline>
        <Grid container spacing={2}>
          <Grid className={style.subContainer} item xs={12} md={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              Bill Type
              <Box sx={{ width: "170px" }}>
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">Type</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    onChange={(e) => setBillType(e.target.value)}
                    value={billType}
                    label="Age"
                  >
                    <MenuItem value={"water"}>water</MenuItem>
                    <MenuItem value={"power"}>power</MenuItem>
                    <MenuItem value={"gas"}>gas</MenuItem>
                  </Select>
                </FormControl>
              </Box>
            </Box>
          </Grid>
          <Grid className={style.subContainer} item xs={12} md={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              Bill Start Date
              <CustomDatePicker
                onClickSubmitButton={(e) => {
                  setStartDate(e.value._i);
                }}
              />
            </Box>
          </Grid>
          <Grid className={style.subContainer} item xs={12} md={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              Bill End Date
              <CustomDatePicker
                onClickSubmitButton={(e) => {
                  setEndDate(e.value._i);
                }}
              />
            </Box>
          </Grid>
          <Grid className={style.subContainer} item xs={12} md={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              Bill Issue Date
              <CustomDatePicker
                onClickSubmitButton={(e) => {
                  setIssueDate(e.value._i);
                }}
              />
            </Box>
          </Grid>
          <Grid className={style.subContainer} item xs={12} md={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              Dead Line Date
              <CustomDatePicker
                onClickSubmitButton={(e) => {
                  setDeadLineDate(e.value._i);
                }}
              />
            </Box>
          </Grid>
          <Grid className={style.subContainer} item xs={12} md={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              <TextField
                fullWidth
                value={billId}
                onChange={(e) => setBillId(e.target.value)}
                id="standard-basic"
                error={billIdErr.trim().length !== 0}
                label={billIdErr ? billIdErr : "Bill Id"}
                variant="standard"
              />
            </Box>
          </Grid>
          <Grid className={style.subContainer} item xs={12} md={6}>
            <span className={style.priceStr}> {mainPriceStr} تومان</span>
            <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              <TextField
                fullWidth
                value={Number(mainPrice).toLocaleString("en-US")}
                onChange={(e) => setMainPrice(e.target.value.replace(/,/g, ""))}
                id="standard-basic"
                error={mainPriceErr.trim().length !== 0}
                label={mainPriceErr ? mainPriceErr : "Main Price"}
                variant="standard"
              />
            </Box>
          </Grid>
          <Grid className={style.subContainer} item xs={12} md={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              <TextField
                fullWidth
                value={tax}
                onChange={(e) => setTax(e.target.value)}
                id="standard-basic"
                error={taxErr.trim().length !== 0}
                label={taxErr ? taxErr : "Tax"}
                variant="standard"
              />
            </Box>
          </Grid>
          <Grid className={style.subContainer} item xs={12} md={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              <TextField
                fullWidth
                value={previusDebt}
                onChange={(e) => setPreviusDebt(e.target.value)}
                id="standard-basic"
                error={previusDebtErr.trim().length !== 0}
                label={previusDebtErr ? previusDebtErr : "Previus Debt"}
                variant="standard"
              />
            </Box>
          </Grid>
          <Grid className={style.subContainer} item xs={12} md={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              <TextField
                fullWidth
                value={consumtionAmount}
                onChange={(e) => setConsumtionAmount(e.target.value)}
                id="standard-basic"
                error={consumtionAmountErr.trim().length !== 0}
                label={consumtionAmountErr ? consumtionAmountErr : "Consumtion Amount"}
                variant="standard"
              />
            </Box>
          </Grid>
          <Grid className={style.subContainer} item xs={12} md={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              <TextField
                fullWidth
                value={discount}
                onChange={(e) => setDiscount(e.target.value)}
                id="standard-basic"
                error={discountErr.trim().length !== 0}
                label={discountErr ? discountErr : "Discount"}
                variant="standard"
              />
            </Box>
          </Grid>
          <Grid className={style.subContainer} item xs={12} md={6}>
            <Box sx={{ display: "flex", justifyContent: "flex-end", alignItems: "center" }}>
              <Button sx={{ margin: "1rem" }} variant="contained" href="#contained-buttons">
                Cancel
              </Button>
              <Button sx={{ margin: "1rem" }} onClick={(e) => onSubmit(e)} variant="contained" href="#contained-buttons">
                Add Bill
              </Button>
            </Box>
          </Grid>
        </Grid>
        {/* <Modal open={true} onClose={() => {}} aria-labelledby="modal-modal-title" aria-describedby="modal-modal-description">
          <Box sx={style}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
              Text in a modal
            </Typography>
            <Typography id="modal-modal-description" sx={{ mt: 2 }}>
              Duis mollis, est non commodo luctus, nisi erat porttitor ligula.
            </Typography>
          </Box>
        </Modal> */}
      </CssBaseline>
    </Wrapper>
  );
};

export default Bill;
