import React, { useEffect, useState } from "react";

import styles from "./familyInfo.module.css";

import Wrapper from "../../../components/wrapper/Wrapper";

import { useLocation } from "react-router-dom";
import customAxiox from "../../../utils/axios";

import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { Button, Chip, Grid, Box, CssBaseline, Slide, Dialog, DialogContent } from "@mui/material";
import CircularProgress from "@mui/material/CircularProgress";
import ButtonGroup from "@mui/material/ButtonGroup";
import { SignalWifiStatusbar4Bar } from "@mui/icons-material";

import BillInfo from "./billsInfo";

const FamilyInfo = () => {
  const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="down" ref={ref} {...props} />;
  });

  const [openModal, setOpenModal] = useState(false);
  const [modalData, setModalData] = useState({});

  const billMockData = {
    date_from: "201548",
    date_to: "5465897",
  };

  const [data, setData] = useState(null);
  const [listData, setListData] = useState([]);

  const location = useLocation();
  const familyId = location.pathname.split("/").pop();

  const getBillData = (billId) => {
    customAxiox(`bill/${billId}`).then((res) => {
      console.log(res.data);
      setOpenModal(true);
      setModalData(res.data);
    });
  };

  const changeBillStatus = (paymentId, status) => {
    console.log("changeing status");
    customAxiox("/payment/pay", {
      method: "POST",
      data: { id: paymentId, status },
    }).then((res) => {
      customAxiox(`payment`, { params: { family: familyId } }).then((res) => {
        setListData(res.data);
      });
    });
  };

  useEffect(() => {
    customAxiox(`family/${familyId}`).then((res) => {
      setData(res.data);
    });
  }, []);

  useEffect(() => {
    customAxiox(`payment`, { params: { family: familyId } }).then((res) => {
      setListData(res.data);
    });
  }, []);

  const payTag = (isPaid = false) => {
    if (isPaid) {
      return <Chip label="paid" color="success" variant="filled" />;
    } else {
      return <Chip label="not paid" color={"error"} variant="filled" />;
    }
  };

  if (!data) {
    return (
      <CssBaseline>
        <Grid style={{ display: "flex", justifyContent: "center", height: "70vh", alignItems: "center" }}>
          <CircularProgress color="primary" />
        </Grid>
      </CssBaseline>
    );
  }

  return (
    <Wrapper>
      <CssBaseline>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between" }}>
              <div>name :</div> {data.family_name}
            </Box>
          </Grid>
          <Grid item xs={12} sm={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between" }}>
              <div>unit name :</div> {data.house.unit_name}
            </Box>
          </Grid>
          <Grid item xs={12} sm={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between" }}>
              <div>start date :</div> <span className={styles.persian_font}> {new Date(data.date_from).toLocaleDateString("fa")}</span>
            </Box>
          </Grid>
          <Grid item xs={12} sm={6}>
            <Box sx={{ display: "flex", justifyContent: "space-between" }}>
              <div>end Date :</div>{" "}
              <span className={styles.persian_font}>{data.date_to ? new Date(data.date_to).toLocaleDateString("fa") : "right now "}</span>
            </Box>
          </Grid>
          <br />
          <br />
          <br />

          <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }} aria-label="simple table">
              <TableHead>
                <TableRow style={{ backgroundColor: "#1769aa" }}>
                  <TableCell align="left">
                    <b> bill price</b>
                  </TableCell>
                  <TableCell align="right">
                    <b> date from</b>
                  </TableCell>
                  <TableCell align="right">
                    <b>date to</b>
                  </TableCell>
                  <TableCell align="right">
                    <b>is paid</b>
                  </TableCell>
                  <TableCell align="right">
                    <b>actions</b>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {listData.map((row, index) => (
                  <TableRow
                    style={{ backgroundColor: index % 2 ? "rgba(100,100,100,0.1)" : "" }}
                    key={row._id}
                    sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                  >
                    <TableCell align="left">{row.amount_paid.toLocaleString("en-US")}</TableCell>
                    <TableCell align="right">
                      {" "}
                      <span className={styles.persian_font}> {new Date(row.date_from).toLocaleDateString("fa")}</span>
                    </TableCell>
                    <TableCell align="right">
                      {" "}
                      <span className={styles.persian_font}> {new Date(row.date_to).toLocaleDateString("fa")}</span>
                    </TableCell>
                    <TableCell align="right">{payTag(row.is_paid)}</TableCell>
                    <TableCell align="right">
                      <ButtonGroup variant="contained" aria-label="text button group">
                        <Button
                          onClick={() => {
                            getBillData(row.bill);
                          }}
                        >
                          More
                        </Button>
                        <Button
                          onClick={() => {
                            changeBillStatus(row._id, !row.is_paid);
                          }}
                        >
                          {row.is_paid ? "up-pay" : "Pay"}
                        </Button>
                      </ButtonGroup>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
        <Dialog open={openModal} transition={Transition}>
          <DialogContent id="modal-slide-description">
            <BillInfo bill={modalData} onClose={setOpenModal} />
          </DialogContent>
        </Dialog>
      </CssBaseline>
    </Wrapper>
  );
};

export default FamilyInfo;
