import * as React from "react";
import Box from "@mui/material/Box";
import BottomNavigation from "@mui/material/BottomNavigation";
import BottomNavigationAction from "@mui/material/BottomNavigationAction";
import RestoreIcon from "@mui/icons-material/Restore";
import FavoriteIcon from "@mui/icons-material/Favorite";
import LocationOnIcon from "@mui/icons-material/LocationOn";
import SettingsIcon from "@mui/icons-material/Settings";
import ReceiptIcon from "@mui/icons-material/Receipt";
import ManageAccountsIcon from "@mui/icons-material/ManageAccounts";
import HomeIcon from "@mui/icons-material/Home";
import BarChartIcon from "@mui/icons-material/BarChart";

import { useNavigate } from "react-router-dom";

import styles from "./nav.module.css";

export default function SimpleBottomNavigation() {
  const [value, setValue] = React.useState(0);

  const navigate = useNavigate();

  return (
    <div className={styles.nav}>
      <Box>
        <BottomNavigation
          showLabels
          value={value}
          onChange={(event, newValue) => {
            setValue(newValue);
          }}
        >
          <BottomNavigationAction onClick={() => navigate("home")} label="Home" icon={<HomeIcon />} />
          <BottomNavigationAction onClick={() => navigate("bills")} label="Bills" icon={<ReceiptIcon />} />
          <BottomNavigationAction disabled={true} onClick={() => navigate("manage")} label="Manage" icon={<ManageAccountsIcon />} />
          {/* <BottomNavigationAction onClick={() => navigate("settings")} label="statics" icon={<BarChartIcon />} /> */}
          <BottomNavigationAction onClick={() => navigate("settings")} label="Settings" icon={<SettingsIcon />} />
        </BottomNavigation>
      </Box>
    </div>
  );
}
