import React, { useState, useEffect } from "react";
import { Grid } from "@mui/material";

import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";

import BasicCard from "../../../components/cards/Card";
import Wrapper from "../../../components/wrapper/Wrapper";

import customAxiox from "../../../utils/axios";

import CircularProgress from "@mui/material/CircularProgress";

import { CssBaseline, Slide, Dialog, DialogContent } from "@mui/material";

import CustomModal from "../../../components/modal/modal";

import BillInfo from "../bill/billsInfo";

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === "dark" ? "#1A2027" : "#fff",
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
}));

const wait = () => {
  return new Promise((res, rej) => {
    setTimeout(() => {
      res(null);
    }, 2000);
  });
};

const Home = () => {
  const [familiesData, setFamiliesData] = useState([]);

  useEffect(() => {
    customAxiox("/family", { method: "GET" }).then(({ data }) => {
      setFamiliesData(data);
    });
  }, []);

  if (familiesData.length === 0) {
    return (
      <CssBaseline>
        <Grid style={{ display: "flex", justifyContent: "center", height: "70vh", alignItems: "center" }}>
          <CircularProgress color="primary" />
        </Grid>
      </CssBaseline>
    );
  }

  return (
    <CssBaseline>
      <Wrapper>
        <Grid flexDirection={"row-reverse"} container spacing={1}>
          {familiesData
            .sort((a, b) => a.house.unit_name - b.house.unit_name)
            .map((item) => {
              return (
                <Grid key={item._id} item lg={3} md={4} sm={6} xs={12}>
                  <BasicCard items={item} />
                </Grid>
              );
            })}
        </Grid>
      </Wrapper>
    </CssBaseline>
  );
};

export default Home;
